div.home {
	width: calc(140px + 100%);  
    top: -50px;
    height: 140vh;
    width: 70vw;
    position: relative;
    background-image: linear-gradient(90deg, white 2px, transparent 2px), linear-gradient(#007bff 1px, transparent 1px), linear-gradient(90deg, #007bff 1px, transparent 1px);
    background-size: 100px 100px, 100px 100px, 20px 20px, 20px 20px;
    background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
    transition: all 0.5s ease-out;
    grid-column: 2;
    grid-row: 3;
    z-index: 1;
}

div.shift {
    background-position: 10px 2px, 0px -30px, 21px 31px, 34px 6px;
    animation: revert 2s cubic-bezier(0.63, -0.99, 0.51, 2.04) 0.2s;
}

@keyframes revert {
    0% {
        background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
    }

    100% {
        background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
    }
}

div#aboutBacksheet {
	position: relative;
	height: auto;
	margin-top: calc(20px + 80%);
	width: 100%;
	background: rgba(245, 245, 245, 0.4);
	border-radius: 5% 5% 5% 5%;
}

div#aboutBacksheet::before {
	content: "";
    position: absolute;
    background-image: linear-gradient(0deg, transparent, rgba(245, 245, 245, 0.5), whitesmoke, rgba(245, 245, 245, 0.5), transparent);
    display: block;
    width: 100%;
    height: 100px;
    transform: scaleY(3.5);
    border-radius: 0% 0% 5% 5%;
    z-index: -2;
}

div#aboutBacksheet::after {
	content: "";
    position: absolute;
    background-image: linear-gradient(0deg, transparent, rgba(0, 0, 0, 0.5), whitesmoke, rgba(0, 0, 0, 0.5), transparent);
    display: block;
    width: 100%;
    height: 100px;
    transform: scaleY(3.5);
    border-radius: 5% 5% 0% 0%;
    z-index: -2;
}

