    div.home {
        left: 0px;
        top: -50px;
        width: 1950px;
        height: 100vh;
        background-image: linear-gradient(90deg, white 2px, transparent 2px), linear-gradient(#007bff 2px, transparent 1px), linear-gradient(90deg, #007bff 2px, transparent 1px);
        background-size: 200px 100px, 100px 100px, 40px 20px, 40px 19px;
    }
}

